    background: #fff;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    padding: 0.25rem 0.19rem 0.5rem 0.24rem;
    box-sizing: border-box;
}

.curve-ser-box {
    position: relative;
    font-size: 0.14rem;
    display: flex;
    justify-content: space-between;
}

.curve-pick {
    ::v-deep .el-range-editor.el-input__inner {
        padding: 0 10px;
    }

    ::v-deep .el-date-editor .el-range-separator {
        padding: 0 5px;
        line-height: 29px;
        width: 5%;
        color: #303133;
    }

    ::v-deep .el-input__inner {
        line-height: 0.32rem;
        height: 0.32rem;
    }
}

.curve-select {
    width: 2.37rem;
    height: 0.32rem;
    margin-right: 0.3rem;

    ::v-deep .el-input__inner {
        line-height: 0.32rem;
        height: 0.32rem;
    }
}


.common-font {
    font-size: 0.14rem;
    display: flex;
    justify-content: space-between;
}

.btn-box {
    position: absolute;
    right: 0;
}

.btn-box span {
    width: 0.65rem;
    height: 0.32rem;
    text-align: center;
    line-height: 0.32rem;
    border-radius: 4px;
    display: inline-block;
    cursor: pointer;
}

.btn-box span:nth-child(1) {
    background: #2e4af7;
    color: #fff;
    margin-right: 0.08rem;
}

.btn-box span:nth-child(2) {
    background: #fff;
    color: #333333;
    border: 1px solid #e3e5ec;
}

.curve-echarts {
    width: 100%;
    min-height: calc(100vh - 3.4rem);
}

.bule {
    color: #2e4af7;
    border-bottom: 2px solid #2e4af7;
}

.violet {
    color: #7937e3;
    border-bottom: 2px solid #7937e3;
}

.yellow {
    color: #ff8446;
    border-bottom: 2px solid #ff8446;
}

.red {
    color: #ff2b73;
    border-bottom: 2px solid #ff2b73;
}

.table-box {
    margin-top: 0.22rem;
}

.point-item {
    ::v-deep .el-form-item__content {
        width: 4rem;

        .el-select {
            width: 100%;
        }
    }
}

.min-item,
.max-item {
    ::v-deep .el-form-item__label {
        text-align: left;
    }
}

.max-item {

    ::v-deep .el-form-item__content {
        .el-date-editor .el-range-input {
            width: 42%;
        }
    }
}
